import React, { useState } from 'react';
import FormGrocery from './FormGrocery';
import '../../assets/main.css';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faTrashAlt } from '@fortawesome/free-solid-svg-icons';
const Grocery = () => {
  const [grocery, setgrocery] = useState([]);
  const addGrocery = text => {
    const newGrocery = [...grocery, { text }];
    setgrocery(newGrocery);
  };
  const remove = index => {
    const newGrocery = [...grocery];
    newGrocery.splice(index, 1);
    setgrocery(newGrocery);
  };
  const clearItem = () => {
    setgrocery([]);
  };
  return (
    <div className='bg-gray-500 h-screen'>
      <h1 className='pt-16 text-center text-3xl text-white'>Grocery List</h1>
      <div className='container mx-auto px-64'>
        <FormGrocery addGrocery={addGrocery} />
        {grocery.map((gros, index) => (
          <div className='flex justify-between'>
            <h1 className='text-green-100 text-3xl text-center'>{gros.text}</h1>
            <button onClick={() => remove(index)}>
              <FontAwesomeIcon className='text-red-500' icon={faTrashAlt} />
            </button>
          </div>
        ))}
        <button
          className='bg-red-700 mt-10 text-3xl rounded-md px-2 text-white w-full'
          onClick={clearItem}
        >
          clear Item
        </button>
      </div>
    </div>
  );
};

export default Grocery;
