import React, { useState } from 'react';
import '../../assets/main.css';
const TodoForm = ({ addTodo }) => {
  const [value, setvalue] = useState('');
  const handleSubmit = e => {
    e.preventDefault();
    if (!value) return;
    addTodo(value);
    setvalue('');
  };
  return (
    <form onSubmit={handleSubmit}>
      <input
        type='text'
        value={value}
        className='shadow appearance-none border border-green-500 rounded  py-2 px-3 text-gray-700 mb-3 leading-tight focus:outline-none focus:shadow-outline w-full'
        placeholder='add something'
        onChange={e => setvalue(e.target.value)}
      />
    </form>
  );
};

export default TodoForm;
