import React, { useState, useEffect } from 'react';
import '../../assets/main.css';
import { useSpring, animated } from 'react-spring';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faDollarSign, faUser } from '@fortawesome/free-solid-svg-icons';
import styled from 'styled-components';

const TipCalculator = () => {
  const [bill, setbill] = useState('');
  const [amount, setamount] = useState('');
  const [selectValue, setselectValue] = useState('');
  const [each, seteach] = useState('');
  const [errorOne, seterrorOne] = useState('');
  const [errorStyle, seterrorStyle] = useState([
    { text: '', background: '', padding: '0rem' }
  ]);
  const moveTopEffect = useSpring({
    marginTop: 0,
    from: { marginTop: 1000 },
    config: { duration: 1000 }
  });
  const opacity = useSpring({
    opacity: 1,
    from: { opacity: 0 },
    delay: 1000,
    config: { duration: 1000 }
  });

  const changeHandler = e => {
    setbill(e.target.value);
  };
  const changeHandlerTwo = e => {
    seteach(e.target.value);
  };
  const changeHandlerSelect = e => {
    setselectValue(e.target.value);
  };
  const Err = styled.div`
    // background-color: ${errorStyle.background};
    background-color: red;
    font-size: 1.5rem;

    text-align: center;
    padding: ${errorStyle.padding};
    
    border-radius: 0.5rem;
    margin-top: 2rem;
    margin-bottom: 2rem;
    color: black;
  `;
  const a = useEffect(() => {
    const timer = setTimeout(
      () =>
        seterrorStyle({
          text: 'field is required',
          background: 'red',
          padding: '0.5rem'
        }),
      3000
    );
    return () => clearTimeout(timer);
  }, []);
  const onClickHandler = e => {
    e.preventDefault();

    if (bill === '' || bill === null) {
      seterrorStyle({
        text: 'field is required',
        background: 'red',
        padding: '0.5rem'
      });
    } else {
      seterrorStyle({
        text: '',
        background: 'transparent',
        padding: '0rem'
      });
      setamount('Tip Amount for each $' + (bill * selectValue) / each);
    }
    // setamount('Tip Amount for each $' + (bill * selectValue) / each);
  };

  return (
    <div className='h-screen bg-blue-200 flex items-center font-serif'>
      <animated.div style={moveTopEffect} className='w-2/5 mx-auto'>
        <animated.h1
          style={opacity}
          className='bg-blue-800 text-white text-center text-2xl p-2 rounded-t-lg '
        >
          TIP CALCULATOR
        </animated.h1>
        <div className='rounded-b-lg bg-white'>
          <form className='mb-4 p-8'>
            <label
              class='block text-gray-700 text-lg font-bold mb-2'
              for='howMuchYourBill'
            >
              How Much Was Your Bill ?
            </label>
            <div className='flex'>
              <div className='bg-blue-800 rounded mx-2 p-3'>
                <FontAwesomeIcon
                  icon={faDollarSign}
                  className=' bg-contain text-4xl text-white mx-2'
                />
              </div>
              <input
                className='shadow appearance-none border w-full rounded  px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline'
                id='howMuchYourBill'
                name='howMuchYourBill'
                type='number'
                onChange={changeHandler}
                placeholder='How Much Was Your Bill ?'
              />
            </div>
            <Err>{errorStyle.text}</Err>
            <label
              class='block text-gray-700 text-lg font-bold mb-2 mt-4'
              for='howManyPeopleSharingTheBill'
            >
              How Many People Sharing The Bill ?
            </label>
            <div className='flex'>
              <div className='bg-blue-800 rounded mx-2 p-2'>
                <FontAwesomeIcon
                  icon={faUser}
                  className=' bg-contain text-4xl text-white mx-2'
                />
              </div>
              <input
                className='shadow appearance-none border w-full rounded  px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline'
                id='howManyPeopleSharingTheBill'
                name='howManyPeopleSharingTheBill'
                type='number'
                onChange={changeHandlerTwo}
                placeholder='How Many People Sharing The Bill ? ?'
              />
            </div>
            <label
              class='block text-gray-700 text-lg font-bold mb-2 mt-4'
              for='choose'
            >
              How Was Your Service
            </label>
            <select
              id='choose'
              name='choose'
              className='w-full h-12'
              onChange={changeHandlerSelect}
            >
              <option disabled selected>
                Choose...
              </option>
              <option value='0.2'>Great</option>
              <option value='0.1'>Good</option>
              <option value='0.02'>Bad</option>
            </select>
            <button
              onClick={onClickHandler}
              className='w-full bg-blue-800 text-white mt-8 text-3xl'
            >
              Calculate
            </button>
          </form>
          <div className='text-center text-2xl'>
            <h2>{amount}</h2>
          </div>
        </div>
      </animated.div>
    </div>
  );
};

export default TipCalculator;
