import React, { useState, useEffect } from 'react';
import '../../assets/main.css';

const FormGrocery = ({ addGrocery }) => {
  const [value, setvalue] = useState([]);
  const [warning, setwarning] = useState([{ text: '', className: '' }]);

  const handelrSubmit = e => {
    e.preventDefault();
    addGrocery(value);
    setvalue('');

    if (!value) {
      setwarning({
        text: 'Please write something',
        className: 'bg-red-500 rounded text-white mb-8 p-2'
      });
      setTimeout(() => {
        setwarning({
          text: '',
          className: ''
        });
      }, 2000);
    } else {
      setwarning({
        text: 'Item added to the list',
        className: 'bg-green-500 rounded text-white mb-8 p-2'
      });
      setTimeout(() => {
        setwarning({
          text: '',
          className: ''
        });
      }, 2000);
    }
  };
  return (
    <form onSubmit={handelrSubmit} className='text-center mt-8'>
      <h1 className={warning.className}>{warning.text}</h1>
      <input
        className='w-full h-10 rounded pl-8 outline-none'
        type='text'
        placeholder='add grocery item'
        value={value}
        onChange={e => setvalue(e.target.value)}
      />
    </form>
  );
};
export default FormGrocery;
