import React, { useState } from 'react';
import '../../assets/main.css';
import { useSpring, animated } from 'react-spring';
// import { v4 as uuidv4 } from 'uuid';
import Todo from './Todo';
import TodoForm from './TodoForm';
export default function Todolist() {
  const [todos, settodos] = useState([
    {
      text: 'Add something',
      isCompleted: false
    }
  ]);
  const moveLeft = useSpring({
    opacity: 1,
    marginLeft: 0,
    marginTop: 0,
    from: { opacity: 0, marginLeft: 1000, marginTop: 1000 },
    config: { duration: 1000 }
  });
  const addTodo = text => {
    const newTodos = [...todos, { text }];
    settodos(newTodos);
  };
  const completeTodo = index => {
    const newTodos = [...todos];
    newTodos[index].isCompleted = true;
    settodos(newTodos);
  };
  const deleteTodo = index => {
    const newTodos = [...todos];
    newTodos.splice(index, 1);
    settodos(newTodos);
  };
  const clearAll = () => {
    const newTodos = [''];
    settodos(newTodos);
  };
  return (
    <animated.div style={moveLeft}>
      <div>
        <h1 className='text-center mt-8 text-3xl mt-64'>To do list</h1>
        <div className='container mx-auto w-1/3 mt-12 mb-64'>
          <TodoForm addTodo={addTodo} />
          <h1>
            {todos.map((todo, index) => (
              <Todo
                key={index}
                index={index}
                todo={todo}
                completeTodo={completeTodo}
                deleteTodo={deleteTodo}
                clearAll={clearAll}
              />
            ))}
          </h1>
          <div className='flex justify-center mt-10'>
            <button
              className='bg-red-500 rounded px-4 text-white'
              onClick={() => clearAll()}
            >
              Clear all
            </button>
          </div>
        </div>
      </div>
    </animated.div>
  );
}
