import React, { useState } from 'react';
import '../../assets/main.css';
import Form from './Form';
// import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
// import { faTrashAlt } from '@fortawesome/free-solid-svg-icons';

const CourseForm = () => {
  const [container, setcontainer] = useState([]);
  const [containerTwo, setcontainerTwo] = useState([]);
  const addFormName = name => {
    const newName = [...container, { name }];
    setcontainer(newName);
  };
  const addFormCourse = course => {
    const newCourse = [...containerTwo, { course }];
    setcontainerTwo(newCourse);
  };
  // const addFormAuthor = author => {
  //   const newForm = [...container.three, { author }];
  //   setcontainer(container.three === newForm);
  // };
  return (
    <div className='h-screen bg-gray-100'>
      <Form
        addFormName={addFormName}
        addFormCourse={addFormCourse}
        // addFormAuthor={addFormAuthor}
      />
      <div className=''>
        {container.map(i => (
          <h1>{i.name}</h1>
        ))}

        {containerTwo.map(i => (
          <h1>{i.course}</h1>
        ))}
      </div>
    </div>
  );
};

export default CourseForm;
