import React, { useState } from 'react';
import '../../assets/main.css';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faSearch } from '@fortawesome/free-solid-svg-icons';
const FilterList = () => {
  const [input, setInput] = useState('');
  const [items, setitems] = useState([
    { name: 'Cake item', price: '$5', image: './cake1.jpg', id: 0 },
    { name: 'Cupcake item', price: '$5', image: './cupcake1.jpg', id: 1 },
    { name: 'Sweets item', price: '$5', image: './sweets1.jpg', id: 2 },
    { name: 'Doughnut item', price: '$5', image: './doughnut1.jpg', id: 3 },
    { name: 'Cake item', price: '$10', image: './cake2.jpg', id: 4 },
    { name: 'Cupcake item', price: '$10', image: './cupcake2.jpg', id: 5 },
    { name: 'Sweets item', price: '$5', image: './sweets2.jpg', id: 6 },
    { name: 'Doughnut item', price: '$10', image: './doughnut2.jpg', id: 7 },
    { name: 'Cake item', price: '$15', image: './cake3.jpg', id: 8 },
    { name: 'Cupcake item', price: '$15', image: './cupcake3.jpg', id: 9 },
    { name: 'Sweets item', price: '$15', image: './sweets3.jpg', id: 10 },
    { name: 'Doughnut item', price: '$15', image: './doughnut3.jpg', id: 11 }
  ]);
  // useEffect(() => {});
  const handleInputChange = e => setInput(e.target.value);

  return (
    <div className='bg-grey-500'>
      <div className='container mx-auto'>
        <h1 className='text-5xl text-center font-serif'>
          Filter <span className='text-pink-500'>List</span>
        </h1>
        <div className='flex justify-center'>
          <FontAwesomeIcon
            icon={faSearch}
            className='h-12 text-5xl bg-pink-500 px-4 text-white rounded-l-lg'
          />
          <input
            id='input'
            type='text'
            name='input'
            className='border border-gray-800 rounded-r-lg h-12 outline-none text-2xl px-4 border-l-0 w-62'
            placeholder='search item'
            onChange={handleInputChange}
          />
        </div>
        <div className='flex flex-wrap justify-center container-lg mt-16'>
          {items
            .filter(item => {
              return (
                item.name.toLowerCase().indexOf(input.toLowerCase()) >= 0 ||
                item.price.toLowerCase().indexOf(input) >= 0
              );
            })

            .map(item => {
              return (
                <div className='my-auto lg:w-3/12 w-64 m-2' key={item.id}>
                  <img
                    src={require(`${item.image}`)}
                    alt=''
                    className='rounded-t-lg h-64 w-full'
                  />
                  <div className='flex flex-wrap justify-between bg-gray-200 rounded-b-lg p-2 mb-4'>
                    <h3>{item.name}</h3>
                    <h3>{item.price}</h3>
                  </div>
                </div>
              );
            })}
        </div>
      </div>
    </div>
  );
};

export default FilterList;
